.sp-summer-money {
    padding-top: 0;
    overflow: hidden;

    .preview-slider__big-img-wrap::after {
        border-radius: 28px; } }

.sp-summer-money__photo,
.sp-summer-money__preview-slider,
.sp-summer-money__preview-slider .slide {
    border-radius: 28px; }

.preview-slider__big-img-wrap::after,
.sp-summer-money__preview-slider .slide {
    overflow: hidden; }

.sp-summer-money-light-section {
    padding-top: 100px;
    padding-bottom: 100px;
    background-color: #FAFBFC;

    &__title {
        position: relative;
        margin-bottom: 40px;
        font-family: $bold-f;
        font-size: 24px;
        letter-spacing: 0.02em;
        line-height: 24px;
        text-align: center; } }

.sp-summer-money-header {
    position: relative;
    height: 980px;
    margin-bottom: 100px;
    color: $light-c;
    background-color: rgba(23, 33, 54, 0.5);
    background-image: url(../images/special-projects/summer-money/sp-summer-money-header-img.jpg);
    background-size: cover;
    background-position: top center;
    background-repeat: no-repeat;

    &__container {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        text-align: center; }

    &__pattern {
        position: absolute;

        &_left {
            width: 440px;
            left: 55px;
            bottom: 30px; }

        &_right {
            width: 500px;
            right: 25px;
            bottom: 35px; } }

    &__title {
        position: relative;
        margin-bottom: 10px;
        font-size: 76px;
        line-height: $mini-lh;
        font-family: $bold-f;
        color: $light-c;
        text-transform: uppercase;

        &::before {
            content: "Г";
            display: block;
            position: absolute;
            top: -50px;
            right: 50px;
            line-height: $micro-lh;
            font-family: $bold-f;
            color: $light-c;
            text-transform: uppercase; }

        &::after {
            content: "";
            display: block;
            width: 60px;
            height: 75px;
            position: absolute;
            top: 10px;
            right: 55px;
            background-image: url(../images/special-projects/summer-money/svg/strikethrough.svg);
            background-size: contain;
            background-repeat: no-repeat; } }

    &__description {
        width: 590px;
        margin-top: 0;
        margin-bottom: 35px;
        font-size: $medium-fz;
        font-family: $add-sb-it-f; }

    &__wobbler {
        margin-bottom: 100px; }

    &__arrow-down {
        width: 55px;
        height: 55px;
        margin-bottom: 50px; } }

.sp-summer-money__statistics-wrap {
