        margin-right: -200px;
        margin-top: -110px; }

    &_2,
    &_4 {
        margin-left: -210px;
        margin-top: -30px; }

    &_3 {
        margin-right: -200px;

        & + .sp-summer-money-where-find__info {
            align-self: flex-end; } } }

.sp-summer-money-where-find__info {
    width: 370px;
    flex-shrink: 0;
    margin-left: 60px; }

.sp-summer-money-where-find__count {
    width: 64px;
    height: 64px;
    margin-bottom: 30px;
    font-size: 40px;
    line-height: 64px;
    font-family: $bold-f;
    color: $light-c;
    text-align: center;
    background-color: $base-c;
    border-radius: 50%; }

.sp-summer-money-where-find__title {
    margin-bottom: 20px;
    font-size: $big-fz;
    font-family: $bold-f; }

.sp-summer-money-where-find__description {
    font-size: $base-fz; }

.sp-summer-money-where-find__table {
    font-size: $mini-fz;

    tr {
        &:not(:last-child) {
            td {
                padding-bottom: 10px; } } }

    td {
        padding: 0;
        vertical-align: baseline;

        &:first-child {
            padding-right: 20px; }

        [class^="icon-"] {
            display: inline-block;
            width: 18px;
            height: 20px;
            margin-right: 5px;
            font-size: 0;
            line-height: 20px;
            color: #0073FF;
            vertical-align: -2px;
            text-align: center;

            &::before {
                font-size: 17px; } }

        div {
            line-height: 1.5; }

        a {
            color: $base-c;
            text-decoration: none;

            &:hover,
            &:focus {
                text-decoration: underline; } } } }

.sp-summer-money-slider {
    &:hover {
        .sp-summer-money-slider__arrow-container {
            opacity: 1; } }

    .slider-container {
        height: 760px; } }

.sp-summer-money-slider__arrow-container {
    position: absolute;
    width: 100%;
    height: 760px;
    top: 0;
    opacity: 0;
    transition: opacity 0.2s;

    .slider-arrow {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        z-index: 2;
        width: 160px;
        height: 100%;
        font-size: 35px;
        color: $light-c;
        border-radius: 28px; }

    .slider-arrow-prev {
        left: 0;
        background: linear-gradient(90deg, rgba(#4c607b, 0.55) 0%, rgba(#4c607b, 0) 100%); }

    .slider-arrow-next {
        right: 0;
        background: linear-gradient(90deg, rgba(#4c607b, 0) 0%, rgba(#4c607b, 0.55) 100%); } }

