.sp-desktop {
    @include media(m) {
        display: none; } }

.sp-mobile {
    margin-top: 50px;
    text-align: center;

    &::after {
        content: "";
        display: block;
        height: 80px;
        margin-top: 90px;
        margin-bottom: 30px;
        background-image: url(../images/svg/header-pattern-mobile.svg);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center; }

    @include media(d) {
        display: none; }

    &__title {
        margin-bottom: 30px;
        font-size: 42px;
        line-height: 1.2;
        font-family: $bold-f; }

    &__wobbler {
        width: max-content;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 20px; }

    &__text {
        max-width: 270px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 60px; }

    &__btn {
        display: block;
        width: 146px;
        margin-left: auto;
        margin-right: auto; } }

.sp img {
    display: block; }

.sp-page {
    padding-top: 0; }

.sp-block {
    &_mt20 {
        margin-top: 20px; }

    &_mt30 {
        margin-top: 30px; }

    &_mt40 {
        margin-top: 40px; }

    &_mt60 {
        margin-top: 60px; }

    &_mt80 {
        margin-top: 80px; }

    &_mt100 {
        margin-top: 100px; }

    p {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0; } } }

.sp-6col {
    width: 640px;
    margin-left: auto;
    margin-right: auto; }

.sp-6col_3-3 {
    display: flex;
    align-items: flex-start;

    .sp-3col {
        width: 310px;
        flex-shrink: 0;
        flex-grow: 0;

        & + .sp-3col {
            margin-left: 20px; } } }

.sp-9col_3-6 {
    display: flex;
    align-items: flex-start;

    .sp-3col {
        width: 310px;
        margin-right: 20px;
        flex-shrink: 0;
        flex-grow: 0; }

    .sp-6col {
        margin: 0; } }

.sp-9col_5-4 {
    display: flex;
    align-items: flex-start;

    .sp-5col {
        width: 530px;
        margin-right: 70px;
        flex-shrink: 0;
        flex-grow: 0;

        & + .sp-block {
            width: 370px; } } }

.sp-6col_4-2 {
    display: flex;
    align-items: flex-start;

    & > :first-child {
        margin-right: 20px; }

    .sp-4col {
        width: 420px;
        flex-shrink: 0; }

    .sp-2col {
        width: 200px;
        position: relative; } }

.sp-12col_4-4-4 {
    display: flex;
    align-items: flex-start;

    & :nth-child(2) {
        margin-left: 20px;
        margin-right: 20px; }

    img {
        width: 420px; } }

.sp-full_4-4-4 {
    display: flex;
    align-items: flex-start;
    justify-content: center;

    &__item {
        flex-shrink: 0;

        &:nth-child(1) {
            figcaption {
                width: 280px;
                margin-left: auto; } }

        &:nth-child(2) {
            margin-left: 20px;
            margin-right: 20px;

            figcaption {
                width: 480px; } }

        &:nth-child(3) {
            figcaption {
                width: 280px; } } }

    figcaption {
        margin-top: 20px;
        font-size: 15px;
        line-height: 20px;
        letter-spacing: 0.08em;
        color: $base-dark-c; }

    img {
        height: 460px; } }

.sp-3col__text {
    padding-right: 40px; }

.sp-8col {
    width: 860px;
    margin-left: auto;
    margin-right: auto; }

.sp-10col {
    width: 1080px;
    margin-left: auto;
    margin-right: auto; }

.sp-bullet-list-wrap {
    &:not(:last-child) {
        margin-bottom: 60px; } }

.sp-bullet-list {
    margin: 0;
    padding: 0 0 0 18px;

    &__title {
        font-family: $bold-f;
        font-size: 18px;
        letter-spacing: 0.02em;
        line-height: 24px;
        text-align: center;
        text-transform: uppercase;
        color: $dark-c; }

    &__item {
        line-height: $base-lh;

        &:not(:last-child) {
            margin-bottom: 20px; } } }

.sp-bullet-item {
    position: relative;

    &::before {
        content: "";
        width: 8px;
        height: 8px;
        position: absolute;
        left: -18px;
        top: 8px;
        background-color: $base-c;
        border-radius: 50%; } }

.sp-mark-text {
    margin: 0;
    padding-left: 30px;
    font-family: $add-sb-it-f;
    font-size: $medium-fz;
    border-left: 2px solid $base-c; }

.sp-preview-slider {
    &_12col {
        .slider-container {
            width: 1300px;
            height: 760px; }

        .sp-preview-slider__arrow-container {
            height: 760px; } }

    &_10col {
        .slider-container {
            width: 1080px;
            height: 760px; }

        .sp-preview-slider__arrow-container {
            height: 760px; } }

    &_6-7col {
        .slider-container {
            width: 720px;
            height: 480px; }

        .sp-preview-slider__arrow-container {
            height: 480px; } }

    &_radius_4 {
        .preview-slider__big-img-wrap,
        .preview-slider__big-img-wrap::after,
        .sp-preview-slider__arrow-container .slider-arrow {
            border-radius: 4px; } }

    &_radius_8 {
        .preview-slider__big-img-wrap,
        .preview-slider__big-img-wrap::after,
        .sp-preview-slider__arrow-container .slider-arrow {
            border-radius: 8px; } }

    &_radius_28 {
        .preview-slider__big-img-wrap,
        .preview-slider__big-img-wrap::after,
        .sp-preview-slider__arrow-container .slider-arrow {
            border-radius: 28px; } }

    &_radius_36 {
        .preview-slider__big-img-wrap,
        .preview-slider__big-img-wrap::after,
        .sp-preview-slider__arrow-container .slider-arrow {
            border-radius: 36px; } }

    &:hover {
        .sp-preview-slider__arrow-container {
            opacity: 1; } } }

.sp-preview-slider__arrow-container {
    position: absolute;
    width: 100%;
    top: 0;
    opacity: 0;
    transition: opacity 0.2s;

    .slider-arrow {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        z-index: 2;
        width: 160px;
        height: 100%;
        font-size: 35px;
        color: $light-c;
        border-radius: 4px; }

    .slider-arrow-prev {
        left: 0;
        background: linear-gradient(90deg, rgba(#4c607b, 0.55) 0%, rgba(#4c607b, 0) 100%); }

    .slider-arrow-next {
        right: 0;
        background: linear-gradient(90deg, rgba(#4c607b, 0) 0%, rgba(#4c607b, 0.55) 100%); } }

.sp__share-block {
    margin: 100px auto 50px;

    .share-widget {
        display: block;
        text-align: center; }

    .share-widget__text {
        display: block;
        margin-bottom: 15px;
        margin-right: 0; }

    .share-widget__links-wrap {
        justify-content: center; }

    .share-widget__link-img {
        width: 37px;
        height: 37px; }

    .share-widget__link:not(:last-child) {
        margin-right: 15px; } }


.sp .direct-speech-author {
    &__avatar {
        display: block;
        width: 110px;
        height: 110px;
        border-radius: 50%;
        border: 1px solid #C3CEDF; } }

.sp .direct-speech_buble {
    display: flex;
    align-items: flex-start;
    margin-left: 0;
    margin-right: 0;
    padding: 0;
    background-color: transparent;
    border-radius: 0;

    .direct-speech-author {
        &__avatar {
            margin-right: 15px;
            margin-bottom: 0;
            flex-shrink: 0;

            & + .direct-speech__text-wrap {
                margin-left: 35px;

                &::before {
                    content: "";
                    display: block;
                    width: 35px;
                    height: 40px;
                    position: absolute;
                    left: -35px;
                    top: 40px;
                    background-image: url(../images/svg/direct-speech-triangle.svg);
                    background-repeat: no-repeat;
                    background-size: contain; } } }

        &__description {
            margin-bottom: 10px;
            font-size: 18px; } }

    .direct-speech__text-wrap {
        position: relative;
        padding: 15px 25px;
        background-color: #f5f6f7;
        border-radius: 4px; } }
