.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    user-select: none;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent; }

.slick-list {
    position: relative;
    display: block;
    overflow: hidden;
    margin: 0;
    padding: 0; }

.slick-slide:focus,
.slick-list:focus {
    outline: none; }

.slick-list.dragging {
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
    transform: translate3d(0, 0, 0); }

.slick-track {
    position: relative;
    top: 0;
    left: 0;
    display: block;
    margin-left: auto;
    margin-right: auto; }

.slick-track:before,
.slick-track:after {
    display: table;
    content: ''; }

.slick-track:after {
    clear: both; }

.slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
    display: none;
    float: left;
    height: 100%;
    min-height: 1px; }

.slick-slide img {
    display: block; }

.slick-slide.dragging img {
    pointer-events: none; }

.slick-initialized .slick-slide {
    display: block; }

.slick-loading .slick-slide {
    visibility: hidden; }

.slick-arrow {
    position: absolute;
    top: 134px;
    color: #AAB2C2;
    font-size: 0;
    z-index: 1;

    &::before {
        font-size: 30px; }

    &:hover {
        color: $light-c;
        background-color: #333E53; }

    &.slick-hidden {
        display: none; } }

.slick-prev {
    left: -62px;

    &::before {
        content: "\e900";
        font-family: $icon-f; } }

.slick-next {
    right: -62px;

    &::before {
        content: "\e902";
        font-family: $icon-f; } }

.slick-dots {
    margin: 0;
    padding: 0;
    list-style: none;
    text-align: center; }

.slick-dots li {
    position: relative;
    display: inline-block; }

.slick-dots li:not(:last-child) button {
    margin-right: 5px; }

.slick-dots li button {
    display: inline-block;
    width: 9px;
    height: 9px;
    font-size: 0;
    line-height: 0;
    border-radius: 50%;
    cursor: pointer;
    background-color: #A1A3A3;

    &:hover {
        background-color: $light-c; } }

.slick-dots li.slick-active button {
    background-color: $light-c; }
