.share-widget {
    @include media(m) {
        text-align: center; }

    @include media(d) {
        display: flex;
        align-items: center;
        justify-content: flex-end; }

    &__text {
        font-size: $mini-fz;
        letter-spacing: 0.08em;

        @include media(m) {
            display: block;
            margin-bottom: 20px; }

        @include media(d) {
            margin-right: 20px; } }

    &__links-wrap {
        display: flex;
        align-items: center;

        @include media(m) {
            justify-content: center; } }

    &__link {
        &:not(:last-child) {
            @include media(m) {
                margin-right: 20px; }

            @include media(d) {
                margin-right: 8px; } } }

    &__link-img {
        display: block;

        @include media(m) {
            width: 42px;
            height: 42px; }

        @include media(d) {
            width: 24px;
            height: 24px; } } }
