.wysiwyg-container {
    line-height: $base-lh;
    color: $dark-c;
    word-break: break-word;

    .left {
        text-align: left; }

    .right {
        text-align: right; }

    .center {
        text-align: center; }

    [class*="wysiwyg-h"] {
        margin-bottom: 20px !important;
        font-family: $bold-f !important;
        line-height: $mini-lh !important;

        @include media(m) {
            margin-top: 30px !important; }

        @include media(d) {
            margin-top: 40px !important; }

        &.wysiwyg-h1 {
            text-transform: uppercase;

            @include media(m) {
                font-size: 24px !important; }

            @include media(d) {
                font-size: 26px !important; } }

        &.wysiwyg-h2 {
            @include media(m) {
                font-size: 20px !important; }

            @include media(d) {
                font-size: 22px !important; } }

        &.wysiwyg-h3 {
            @include media(m) {
                font-size: 18px !important; }

            @include media(d) {
                font-size: 20px !important; } }

        &:first-child {
            margin-top: 0 !important; }

        &:last-child {
            margin-bottom: 0 !important; } }

    b,
    strong {
        font-family: $bold-f !important;
        font-weight: bold; }

    i {
        font-style: italic; }

    a,
    a:link,
    a:visited {
        color: $base-c;
        text-decoration: none;

        &:hover {
            text-decoration: underline; } }

    p {
        margin: 0 0 20px 0 !important;
        font-size: $base-fz;

        &:last-child {
            margin-bottom: 0 !important; } }

    ul,
    ol {
        padding-left: 0 !important;
        font-size: $base-fz !important;
        line-height: $base-lh !important;

        @include media(m) {
            margin: 20px 0 !important;
            padding-left: 1.5em !important; }

        @include media(d) {
            margin: 20px 0 40px 40px !important; }

        li {
            margin-top: .5em !important;
            padding-left: 5px !important;

            &:first-child {
                margin-top: 0 !important; } }

        ul,
        ol {
            @include media(m) {
                margin: 20px 0 20px 20px !important; }

            @include media(d) {
                margin: 20px 0 40px 40px !important; } } }

    & > ul,
    & > ol {
        &:first-child {
            margin-top: 0 !important; }

        &:last-child {
            margin-bottom: 0 !important; } }

    ul {
        li {
            list-style: disc !important; } }

    ol {
        li {
            list-style: decimal !important; } } }
